import React, {Component} from 'react';
import {Card,
         CardMedia,
         CardTitle,
} from 'material-ui/Card'
import TextField from "material-ui/TextField"
import Paper from "material-ui/Paper"
import RaisedButton from "material-ui/RaisedButton"
import call from "./api"
import update from 'immutability-helper'

class Home extends  Component{
    constructor(props) {
        super(props);
        this.state = {
            Q:null,
            A:null,
        }

        this.styles = {
            searchButton: {
                marginLeft: "20%",
                marginRight: "20%",
            },
            card:{
                marginLeft: "20%",
                marginRight: "20%",
            },
            searchText:{
                paddingLeft: "15%"
            },
            div: {
                marginBottom: "70%"
            },
            paperQ: {
                fontSize: "x-large",
                marginTop: "30",
                paddingLeft:"15%",
                paddingRight: "15%",
                marginLeft: "10%",
                marginRight: "10%"
            },
            paperA: {
                fontSize: "larger",
                marginTop: "30",
                marginRight: "15%",
                marginLeft: "15%",
            }
        }
        this.handleChange = (e) => {
            this.setState(update(this.state,{A:{$set:{}}}));
            this.setState(update(this.state,{Q:{$set:e.currentTarget.value}}))
            console.log("change state: ",this.state)

        }

        this.handleClick = () => {
            this.setState(update(this.state,{A:{$set:null}}));
            call("answers",
                this.state.Q,
                (data,err) =>{
                    if(data){
                        this.setState(update(this.state,{A:{$set:data}}));
                        console.log("state" ,this.state);
                        //for(let answer in this.state.A){
                          //  console.log("state" ,this.state);
                        //}
                    }
                }
            )
        }
    }
    render() {
        return(
            <div>
                <div style={this.styles.div}>
                    <Card  style={this.styles.card}>
                        <CardMedia overlay={<CardTitle title="BiBot" />} >
                            <img src="http://puu.sh/qsW1t/c30576d658.jpg"/>
                        </CardMedia>
                        <div>
                            <TextField defaultValue="search"
                                       onChange={this.handleChange}
                                       style={this.styles.searchText}
                            />
                            <RaisedButton label="search"
                                          primary={true}
                                          style={this.styles.searchButton}
                                          onClick={this.handleClick}
                            />
                        </div>
                    </Card>
                    <div>
                        {
                            (() => {
                                console.log("now state",this.state);
                                let component = [];
                                if(this.state.Q){
                                    component.push(<Paper style={this.styles.paperQ}>{"Q: "+this.state.Q}</Paper>);
                                }
                                else
                                    return null;
                                if(this.state.A){
                                    for(let answer in this.state.A){
                                        component.push(
                                            <Paper style={this.styles.paperA}>{"A: "+ answer}</Paper>
                                        )
                                    }
                                    return component;
                                }
                                else{
                                    component.push(
                                        <Paper style={this.styles.paperA}>{"抱歉，未找到答案。"}</Paper>
                                    )
                                    return component;
                                }
                            })()
                        }
                    </div>
                </div>
            </div>
        )
    }
}


export default Home;


